.App {
  text-align: center;
}

.mainNav{
  max-height: 80px;
  z-index: 1;
}
.nav{
  display: flex;
  justify-content: space-around;
  height: 80px;
  background: #12124b;
  align-items: center;
}
.nav h3{
  font-size: 20px;
  color: #fff;
}
.nav .carts{
  background: #fff;
  width: 100px;
  border-radius: 5px;
  align-items: center;
  cursor: pointer;
  display: flex;
  justify-content: space-around;
}
.nav .carts img{
  width: 30px;
  
}
.mainNav .myCarts{
  justify-content: right;
  z-index: 1;
  /* margin-top: -15px; */
  display: flex;
  flex-direction: row-reverse;
  max-height: 500px;
}
.scroll{
  overflow-y: scroll;
  max-width: 517px;
  margin-right: 100px;
}
.cartStyle{
  width: 500px;
  margin: 0px;
  
  display: flex;
  height: 100px;
  background: #f5f5f5;
  box-shadow: 0px 3px 5px 2px #bbb;
  justify-content: space-between;
  align-items: center;
  z-index: 1;
}
.cartStyle img{
  max-height: 90px;
  width: 90px;
}
.cartStyle h5{
  padding: 5px 15px;
  font-size: 20px;
  cursor: pointer;
  margin: 0px;
  background: #12124b;
  color: #fff;
}

.addItems form{
  display: flex;
  justify-content: space-around;
  margin-top: 40px;
  z-index: -1;
}
.addItems .form{
  width: 200px;
  height: 300px;
  padding: 0px;
  border-radius: 15px;
  box-shadow: 0px 0px 20px 1px #ccc;

  z-index: -1;
}
.addItems img{
  width: 100%;
  height: 200px;
  margin: 0px;
  z-index: -1;
}
.addItems h4{
  font-size: 18px;
  margin: 0px
}
.addItems h2{
  padding: 0px;
}
.addItems button{
  color: #fff;
  background: orangered;
  padding: 8px 16px ;
  border-radius: 7px;
  border-width: 0px;
  margin-top: 0px;
  cursor: pointer;
  outline: none;
}